import React from "react";
import Card from "./Card";
import data from "./data";

const Bottom = () => {
    const items = data.filter((item) => {
        return item.list === "b";
    });
    console.log(items);
    return (
        <div>
            <ul className="contents">
                {items.map((item) => {
                    return <Card item={item} />;
                })}
            </ul>
        </div>
    );
};

export default Bottom;
