import React from "react";
import Card from "./Card";
import data from "./data";

const Top = () => {
    const items = data.filter((item) => {
        return item.list === "k";
    });
    console.log(items);
    return (
        <div>
            <ul className="contents">
                {items.map((item) => {
                    return <Card item={item} />;
                })}
            </ul>
        </div>
    );
};

export default Top;

