import React from "react";

// Import Swiper React components
import { Swiper, SwiperSlide } from "swiper/react";
// Import Swiper styles
import "swiper/css";
import "swiper/css/pagination";
import "swiper/css/navigation";
import "./styles.css";
import Card from "./Card";
import data from "./data";

// import required modules
import { Autoplay, Pagination, Navigation } from "swiper/modules";

const Home = () => {
    const bg1 = {
            "background-image": `url(${process.env.PUBLIC_URL}/img/p1.png)`,
        },
        bg2 = {
            "background-image": `url(${process.env.PUBLIC_URL}/img/p2.png)`,
        },
        bg3 = {
            "background-image": `url(${process.env.PUBLIC_URL}/img/p3.png)`,
        };
    return (
        <div>
            <Swiper
                spaceBetween={30}
                centeredSlides={true}
                loop={true}
                autoplay={{
                    delay: 4500,
                    disableOnInteraction: false,
                }}
                pagination={{
                    clickable: true,
                    dynamicBullets: true,
                }}
                navigation={true}
                modules={[Autoplay, Pagination, Navigation]}
                className="mySwiper"
            >
                <SwiperSlide className="bg" style={bg1}></SwiperSlide>
                <SwiperSlide className="bg" style={bg2}></SwiperSlide>
                <SwiperSlide className="bg" style={bg3}></SwiperSlide>
            </Swiper>

            <ul className="contents">
                {data.map((item) => {
                    return <Card item={item} />;
                })}
            </ul>
        </div>
    );
};

export default Home;
